











export default {
	data() {
		return {
			tabIndex: 1,
		};
	},
	methods:{
		change_nav(index){
			this.tabIndex = index;
			this.$store.commit("change_page",index);
		}
	},
	computed:{
		item_width(){
			let length = this.$store.state.footer_store.footer_nav.length;
			switch (length){
				case 1:
					return '100%'
					break;
				case 2:
					return '50%'
					break;
				case 3:
					return '33.3%'
					break;
				case 4:
					return '25%'
					break;
				case 5:
					return '20%'
					break;
				default:
					break;
			}
		},
		footer_nav(){
			return this.$store.state.footer_store.footer_nav;
		},
		now_index(){
			return this.$store.state.footer_store.now_page_index;
		}
	}
}
