




  export default {
    name: 'iconfont',
    props: {
			icon: {
				type: String,
				default: '&#xe605;'
			},
      /**
       * 自定义字体图标库前缀
       */
      prefixClass: {
        type: String,
        default: 'iconfont'
      },
      /**
       * 图标类型
       */
      type: String,
      /**
       * 图标颜色
       */
      color: {
        type: String,
        default: '#fff'
      },
      /**
       * 图标大小
       */
      size: {
        type: [Number, String],
        default: '24'
      }
    },
    computed: {
      fontSize() {
        return `${this.size}px`
      }
    },
    methods: {
      $_click(e) {
        this.$emit('click',e)
      }
    }
  }
